import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import MessagePlugin from './plugins/message'
import './assets/styles.css'
import Util from './plugins/utils'
Vue.use(MessagePlugin);
Vue.use(Util);
Vue.config.productionTip = false;


//
(function() {
  const originalConsoleLog = console.log;

  console.log = function(...args) {
    // 
    const stack = new Error().stack.split('\n');
    // 
    const callerLine = stack[2]; // stack[0]  Error stack[1] stack[2] 
    const callerInfo = callerLine.match(/at\s+(?:.*\s+)?\((.*):(\d+):(\d+)\)/);

    if (callerInfo) {
      const [, file, line, column] = callerInfo;
      let fileN = file.substring(file.indexOf("src"));
      // 
      args.push(`(File: ${fileN}, Line: ${line}, Column: ${column})`);
    }

    //  console.log 
    originalConsoleLog.apply(console, args);
  };
})();


const vm = new Vue({
  render: h => h(App),
  router,
  store
}).$mount('#app')


export default vm;